import 'regenerator-runtime'; /* for async await transpile */
import '../styles/main.css';
import {idbHelper} from './idb-helper';

document.addEventListener('DOMContentLoaded', async () => {
    const favoritesContainer = document.getElementById('favorite-restaurant-container');
        if (!favoritesContainer) {
            console.error('Favorites container element not found');
            return;
        }

  const renderFavorites = async () => {
    try {
        const favorites = await idbHelper.getAllFavorites();
        if (favorites.length === 0) {
            favoritesContainer.innerHTML = '<p>No favorite restaurants found</p>';
        } else {
            favoritesContainer.innerHTML = '';
            favorites.forEach(restaurant => {
                const restaurantCard = document.createElement('div');
                restaurantCard.classList.add('restaurant-card');

                restaurantCard.innerHTML = `
                    <img tabindex="0" src="https://restaurant-api.dicoding.dev/images/medium/${restaurant.pictureId}" alt="${restaurant.name}">
                    <div class="restaurant-card-content">
                        <h2 tabindex="0">${restaurant.name}</h2>
                        <p tabindex="0">${restaurant.description.substring(0, 100)}...</p>
                        <p class="city" tabindex="0">City: ${restaurant.city}</p>
                        <p class="rating" tabindex="0">Rating: ${restaurant.rating}</p>
                        <div class="cta-container">
                            <a href="detail.html?id=${restaurant.id}" class="cta">View Details</a>
                            <button class="delete-button" data-id="${restaurant.id}">Delete</button>
                        </div>
                    </div>
                `;

                favoritesContainer.appendChild(restaurantCard);
            });
        }
    } catch (error) {
        console.error('Failed to fetch favorite restaurants:', error);
    }

    document.querySelectorAll('.delete-button').forEach(button => {
        button.addEventListener('click', async (event) => {
            const restaurantId = event.target.dataset.id;
            await idbHelper.deleteFavorite(restaurantId);
            renderFavorites(); 
        });
    });
};

await renderFavorites();

const navToggle = document.querySelector('.nav-toggle');
const navMobile = document.querySelector('.nav-mobile');

if (navToggle && navMobile) {
    navToggle.addEventListener('click', () => {
        const isExpanded = navToggle.getAttribute('aria-expanded') === 'true';
        navToggle.setAttribute('aria-expanded', !isExpanded);
        navMobile.classList.toggle('nav-mobile-active');
        navMobile.setAttribute('aria-hidden', isExpanded);
    });

    navToggle.addEventListener('keydown', (event) => {
        if (event.key === 'Enter' || event.key === ' ') {
            event.preventDefault();
            navToggle.click();
        }
    });
} else {
    console.error('Elemen navigasi tidak ditemukan');
}

const header = document.querySelector('.header');
const scrollTrigger = 50;

window.addEventListener('scroll', function() {
    if (window.scrollY > scrollTrigger) {
        header.classList.add('header--minimized');
    } else {
        header.classList.remove('header--minimized');
    }
});

const logo = document.querySelector('.logo');
    if (logo) {
        logo.addEventListener('click', () => {
            window.location.href = '/';
        });
    }
    
});
