  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.skip-to-content:focus {
  top: 10px;
  outline: 2px solid #333;
  outline-offset: 2px;
}

body {
  font-family: 'Poppins', Arial, sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f4f4f4;
  color: #333;
}

:focus {
  outline: 2px ;
  outline-offset: 2px;
}

.nav-toggle:focus {
  outline: 2px ;
}

.nav-mobile a:focus {
  outline: 2px ;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 2rem;
  background-color: #333; 
  color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 10;
  transition: all 0.5s ease;
}

.header--minimized {
  height: 80px; 
  padding: 5px 10px;
}

header .logo {
  display: flex;
  align-items: center;
  font-size: 2rem;
  padding: 1rem 2rem;
  font-weight: bold;
  min-width: 44px;
  min-height: 44px;
}

header div span:hover {
  color: #ffd700;
}

header .logo img {
  height: 40px;
  margin-right: 0.5rem;
  min-width: 44px;
  min-height: 44px;
}

header nav ul {
  list-style: none;
  display: flex;
  gap: 1rem;
  margin: 0;
  padding: 1rem 2rem;
  right: 50%;
}

header nav ul li a {
  display: inline-block;
  text-decoration: none;
  color: white;
  margin-bottom: 10px; 
  padding: 12px 20px;
  font-size: 1rem;
  transition: color 0.3s ease;
  min-width: 44px;
  min-height: 44px;
}

header nav ul li a:hover {
  color: #ffd700;
}

.nav-desktop {
  display: none;
}

.nav-mobile {
  display: none;
  flex-direction: column;
  background-color: #333;
  position: absolute;
  top: 60px;
  right: 0;
  width: 200px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  z-index: 20;
}

.nav-mobile.nav-mobile-active {
  display: flex;
}

.nav-mobile ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-mobile ul li {
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.nav-toggle {
  background: none;
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  z-index: 20;
  position: relative;
}

.nav-mobile ul li a {
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
  padding: 12px 20px;
  margin-bottom: 10px; 
  text-decoration: none;
  color: white;
}

a, button, input[type="button"], input[type="submit"] {
  display: inline-block;
  padding: 10px 20px;
  min-width: 44px;
  min-height: 44px;
}

a {
  display: inline-block;
}

button, .nav-toggle, .nav-mobile a, input[type="text"], textarea {
  min-width: 44px;
  min-height: 44px;
  padding: 8px; 
  box-sizing: border-box;
  margin: 8px 0;
}

.hero {
  position: relative;
  width: 100%;
}

.hero img {
  width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}

.hero-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
  text-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  padding: 1rem;
  box-sizing: border-box;
}

.hero-text h1 {
  font-size: 3rem;
  margin: 0;
}

.hero-text p {
  font-size: 1.2rem;
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 20px;
}

.main {
  padding: 20px;
}

.restaurant-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 3rem;
  padding: 2rem;
  width: 100%;
  max-width: 1200px;
}

.restaurant-card {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.restaurant-card:hover {
  transform: scale(1.05);
}

.restaurant-card img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.restaurant-card-content {
  padding: 1.5rem;
}

.restaurant-card h2 {
  margin: 0 0 1rem;
  font-size: 1.5rem;
  color: #333;
}

.restaurant-card p {
  margin: 0.5rem 0;
  color: #666;
}

.restaurant-card .city, .restaurant-card .rating {
  font-size: 0.9rem;
  color: #999;
}

.restaurant-card a {
  font-weight: bold;
}

.restaurant-detail-container h2 {
  zoom: 1.5;
}

.restaurant-card .cta {
  display: inline-block;
  background-color: #747474;
  color: #333;
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.restaurant-card .cta:hover {
  background-color: #ffc107;
}

.cta-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.delete-button {
  display: inline-block;
  min-width: 44px;        
  min-height: 44px;
  font-size: 16px;
  background-color: #f03d3d;
  color: #ffffff;
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s;
}

.delete-button:hover {
  background-color: #313131;
  font-weight: bold;
}

#favorite-button {
  display: inline-flex;
  min-width: 44px;
  min-height: 44px;
  font-size: 16px;
  background-color: #ff6f61;
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 200px;
  transition: background-color 0.3s, color 0.3s;
}

#favorite-button:hover {
  background-color: #ff3b2f;
  color: #000;
  font-weight: bold;
}

.footer {
  background-color: #333;
  color: #fff;
  padding: 20px 0;
  text-align: center;
  width: 100%;
}

footer p:hover {
  color: #ffd700;
}

@media screen and (max-width: 767px) {
  nav.nav-desktop {
    display: none;
  }

  .nav-toggle {
    display: block;
  }

  .nav-toggle:hover {
    color: #ffd700;
  }

  .nav-mobile {
    display: none; 
  }

  .hero-text {
    font-size: 0.5rem;
  }

  .hero-text h1 {
    font-size: 4vw;
    margin-bottom: 0.5rem;
  }

  .hero-text p {
    font-size: 2vw;
  }

  .restaurant-container {
    display: grid;
    grid-template-columns: 1fr; 
    gap: 1rem; 
    padding: 1rem; 
    width: 100%; 
    max-width: 100%;
  }

  header .logo {
    font-size: 1.5rem;
  }

  header .logo img {
    height: 30px;
  }

  header {
    padding: 1rem;
  }

  .nav-mobile ul li a {
    padding: 0.75rem;
    color: #ffd700;
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .nav-desktop {
    display: flex;
  }

  .nav-toggle {
    display: none;
  }

  .nav-mobile {
    display: none;
  }

  .hero-text h1 {
    font-size: 2.5rem;
  }

  .hero-text p {
    font-size: 1.1rem;
  }

  .restaurant-container {
    display: grid;
    grid-template-columns: 1fr; 
    gap: 1rem; 
    padding: 1rem; 
    width: 100%; 
    max-width: 100%;
  }
}

@media only screen and (min-width: 1024px) {
  .nav-desktop {
    display: flex;
  }

  .nav-mobile {
    display: none;
  }

  .nav-toggle {
    display: none;
  }

  .hero-text h1 {
    font-size: 3rem;
  }

  .hero-text p {
    font-size: 1.2rem;
  }
}


